.videoElementWrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: -1000;
}

.videoElement {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (min-aspect-ratio: 16/9) {
  .videoElement {
    height: 300%;
    top: -100%;
  }
}

@media (max-aspect-ratio: 16/9) {
  .videoElement {
    width: 300%;
    left: -100%;
  }
}

@media (min-width: 767px) {
  .videoElement {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
}



